header{
  background-color: var(--bg-light-color);
}
.header__ul{
  padding: 46px 101px 126px 0;
  display: grid;
  grid-template-columns: repeat(6, auto);
  justify-content: space-evenly;
  align-items: baseline;
  li{
    list-style: none;
    button{
      width: 150px;
      height: 38px;
      font-size: 18px;
      line-height: 21.88px;
      border-radius: 10px;
    }
  }
}